<template>
  <div style="width: 600px">
    <el-form label-width="110px">
      <el-form-item label="类型" prop="type">
        <el-radio-group v-model="sm9Form.type" @change="radioChange">
          <el-radio :label="0">获取公开参数</el-radio>
          <el-radio :label="1">加密</el-radio>
          <el-radio :label="2">解密</el-radio>
          <el-radio :label="3">签名</el-radio>
          <el-radio :label="4">验签</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <el-form :model="sm9Form" ref="sm9FormRef" :rules="sm9FormRules" label-width="110px">
      <el-form-item label="密钥标识" prop="keyId">
        <el-input v-model.trim="sm9Form.keyId"></el-input>
      </el-form-item>
      <el-form-item label="用户标识" prop="idb">
        <el-input v-model.trim="sm9Form.idb"></el-input>
      </el-form-item>
      <div v-if="isShow === 1">
        <el-form-item label="加密明文" prop="plaintext">
          <el-input type="textarea" :rows="6" v-model.trim="sm9Form.plaintext"></el-input>
        </el-form-item>
      </div>
      <div v-if="isShow === 2">
        <el-form-item label="加密密文" prop="ciphertextBlob">
          <el-input type="textarea" :rows="6" v-model.trim="sm9Form.ciphertextBlob"></el-input>
        </el-form-item>
      </div>
      <div v-if="isShow === 3 || isShow === 4">
        <el-form-item label="签名原文" prop="digest">
          <el-input type="textarea" :rows="6" v-model.trim="sm9Form.digest"></el-input>
        </el-form-item>
      </div>
      <!--签名-->
      <div v-if="isShow === 4">
        <el-form-item label="签名结果" prop="valueH">
          <el-input type="textarea" :rows="6" v-model.trim="sm9Form.valueH"></el-input>
        </el-form-item>
      </div>
      <el-form-item label="结果" prop="result0" v-if="isShow === 0">
        <el-input type="textarea" :readonly="true" :rows="14" v-model.trim="sm9Form.result0"></el-input>
      </el-form-item>
      <el-form-item label="结果" prop="result1" v-if="isShow === 1">
        <el-input type="textarea" :readonly="true" :rows="14" v-model.trim="sm9Form.result1"></el-input>
      </el-form-item>
      <el-form-item label="结果" prop="result2" v-if="isShow === 2">
        <el-input type="textarea" :readonly="true" :rows="14" v-model.trim="sm9Form.result2"></el-input>
      </el-form-item>
      <el-form-item label="结果" prop="result3" v-if="isShow === 3">
        <el-input type="textarea" :readonly="true" :rows="14" v-model.trim="sm9Form.result3"></el-input>
      </el-form-item>
      <el-form-item label="结果" prop="result4" v-if="isShow === 4">
        <el-input type="textarea" :readonly="true" :rows="14" v-model.trim="sm9Form.result4"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitSm9">提交</el-button>
        <el-button @click="resetForm('sm9FormRef')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      const checkSource = (rule, value, cb) => {
        const reg = /^[0-9a-fA-F]+$/;
        if (reg.test(value)) {
          return cb()
        }
        cb(new Error('请输入16进制的字符串'))
      };
      return {
        sm9Form: {
          keyId: '',
          idb: '',
          plaintext: '',
          ciphertextBlob: '',
          digest: '',
          valueH: '',
//          valueS: '',
          type: 0,
          result0: '',
          result1: '',
          result2: '',
          result3: '',
          result4: '',
        },
        isDisabled: true,
        isShow: 0,
        sm9FormRules: {
          keyId: [{required: true, message: '必填项不能为空!',trigger: 'blur'}],
          idb: [{required: true, message: '必填项不能为空！', trigger: 'blur'}],
          plaintext: [{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
          ciphertextBlob: [{required: true,validator: this.Global.checkBase64, trigger: 'blur'}],
          digest: [{required: true,message: '请输入16进制的字符串', validator: checkSource,trigger: 'blur'}],
          valueH: [{required: true,validator: this.Global.checkBase64,trigger: 'blur'}],
        }
      }
    },
    methods: {
      async submitSm9() {
        let data;
        if (this.sm9Form.type === 0) {
          data = {
            'keyId': this.sm9Form.keyId,
            'idb': this.sm9Form.idb
          }
        } else if (this.sm9Form.type === 1) {
          data = {
            'keyId': this.sm9Form.keyId,
            'idb': this.sm9Form.idb,
            'plaintext': this.sm9Form.plaintext
          }
        } else if (this.sm9Form.type === 2) {
          data = {
            'keyId': this.sm9Form.keyId,
            'idb': this.sm9Form.idb,
            'ciphertextBlob': this.sm9Form.ciphertextBlob
          }
        } else if (this.sm9Form.type === 3) {
          data = {
            'keyId': this.sm9Form.keyId,
            'idb': this.sm9Form.idb,
            'digest': this.sm9Form.digest
          }
        } else if (this.sm9Form.type === 4) {
          data = {
            'keyId': this.sm9Form.keyId,
            'idb': this.sm9Form.idb,
            'digest': this.sm9Form.digest,
            'sign': this.sm9Form.valueH
          }
        }
        this.$refs.sm9FormRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.post(`arithmetic/sm9?type=${this.sm9Form.type}`, data);
          if (res.code === 0) {
//            this.$message.success('请求成功！');
            const result = res.data;
            if (this.sm9Form.type === 0) {
              this.sm9Form.result0 = ("签名主公钥:" + "\n" + result.pubSign + "\n\n" + "加密主公钥:" + "\n" + result.pubEnc + "\n\n" + "签名私钥:" + "\n" + result.priSign + "\n\n" + "加密私钥:" + "\n" + result.priEnc);
            } else if (this.sm9Form.type === 1) {
              this.sm9Form.result1 = ('加密密文:' + "\n" + result.ciphertextBlob)
            } else if (this.sm9Form.type === 2) {
              this.sm9Form.result2 = ('加密明文:' + "\n" + result.plaintext)
            } else if (this.sm9Form.type === 3) {
              this.sm9Form.result3 = ('签名结果:' + "\n" + result.sign)
            } else if (this.sm9Form.type === 4) {
              this.sm9Form.result4 = result.value === 'true' ? '验签一致' : '验签不一致';
            }
          } else {
            if (this.sm9Form.type === 0) {
              this.sm9Form.result0 = '获取公开参数失败！'
            } else if (this.sm9Form.type === 1) {
              this.sm9Form.result1 = '加密失败！'
            } else if (this.sm9Form.type === 2) {
              this.sm9Form.result2 = '解密失败！'
            } else if (this.sm9Form.type === 3) {
              this.sm9Form.result3 = '签名失败！'
            } else if (this.sm9Form.type === 4) {
              this.sm9Form.result4 = '验签失败！'
            }
          }
        })
      },
      radioChange(value) {
        this.isShow = value;
        this.$refs.sm9FormRef.clearValidate();
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>
