<template>
  <div style="width: 600px">
    <el-form :model="sm3Form" ref="sm3FormRef" :rules="sm3FormRules" label-width="110px">
      <el-form-item label="类型" prop="type">
        <el-radio-group v-model="sm3Form.type">
          <el-radio :label="1">加密</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="内容" prop="source">
        <el-input type="textarea" maxlength="2048" show-word-limit :rows="8" v-model.trim="sm3Form.source"></el-input>
      </el-form-item>
      <el-form-item label="结果" prop="result">
        <el-input type="textarea" :readonly="true" :rows="8" v-model.trim="result"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitSm3">提交</el-button>
        <el-button @click="resetForm('sm3FormRef')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      const checkSource = (rule, value, cb) => {
        const reg = /^[0-9a-fA-F]+$/;
        if (reg.test(value)) {
          return cb()
        }
        cb(new Error('请输入16进制的字符串'))
      };
      return {
        sm3Form: {
          source: '',
          type: 1,
        },
        result: '',
        sm3FormRules: {
          source: [{required: true, message: '请输入16进制的字符串', validator: checkSource,trigger: 'blur'}]
        }
      }
    },
    methods: {
      async submitSm3() {
        this.$refs.sm3FormRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.post('arithmetic/sm3',
            {source: this.sm3Form.source}
          );
          if (res.code === 0) {
//            this.$message.success('请求成功！');
            this.result = res.data
          } else {
            this.result ='加密失败！';
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.result = '';
      }
    }
  }
</script>
