<template>
  <div style="width: 600px">
    <el-form label-width="110px">
      <el-form-item label="类型" prop="type">
        <el-radio-group v-model="sm4Form.type" @change="radioChange">
          <el-radio :label="1">加密</el-radio>
          <el-radio :label="2">解密</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <el-form :model="sm4Form" :rules="sm4FormRules" ref="sm4FormRef" label-width="110px">
      <el-form-item label="算法" prop="seleValue">
        <el-select placeholder="请选择" v-model="sm4Form.seleValue" @change="seleChange">
          <el-option label="SM4/ECB/NOPADDING" value="SM4/ECB/NOPADDING"></el-option>
          <el-option label="SM4/ECB/PKCS5PADDING" value="SM4/ECB/PKCS5PADDING"></el-option>
          <el-option label="SM4/ECB/PKCS7PADDING" value="SM4/ECB/PKCS7PADDING"></el-option>
          <el-option label="SM4/CBC/NOPADDING" value="SM4/CBC/NOPADDING"></el-option>
          <el-option label="SM4/CBC/PKCS5PADDING" value="SM4/CBC/PKCS5PADDING"></el-option>
          <el-option label="SM4/CBC/PKCS7PADDING" value="SM4/CBC/PKCS7PADDING"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="密钥" prop="pubenc">
        <el-input v-model.trim="sm4Form.pubenc"></el-input>
      </el-form-item>
      <el-form-item label="向量" prop="iv" v-if="isIv">
        <el-input v-model.trim="sm4Form.iv"></el-input>
      </el-form-item>
      <div v-if="isShow === 1">
        <el-form-item label="加密内容" prop="sourceEn">
          <el-input type="textarea" :rows="8" v-model.trim="sm4Form.sourceEn"></el-input>
        </el-form-item>
        <el-form-item label="结果" prop="resultEn">
          <el-input type="textarea" :readonly="true" :rows="8" v-model.trim="sm4Form.resultEn"></el-input>
        </el-form-item>
      </div>
      <div v-else-if="isShow === 2">
        <el-form-item label="解密内容" prop="sourceDe">
          <el-input type="textarea" :rows="8" v-model.trim="sm4Form.sourceDe"></el-input>
        </el-form-item>
        <el-form-item label="结果" prop="resultDe">
          <el-input type="textarea" :readonly="true" :rows="8" v-model.trim="sm4Form.resultDe"></el-input>
        </el-form-item>
      </div>
      <el-form-item>
        <el-button type="primary" @click="submitSm4">提交</el-button>
        <el-button @click="resetForm('sm4FormRef')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      const checkPubenc = (rule, value, cb) => {
        const reg = /^([0-9a-fA-F]){32}$/;
        if (reg.test(value)) {
          return cb()
        }
        cb(new Error('请输入32位16进制的密钥'))
      };
      const checkIv = (rule, value, cb) => {
        const reg = /^([0-9a-fA-F]){32}$/;
        if (reg.test(value)) {
          return cb()
        }
        cb(new Error('请输入32位16进制的向量'))
      };
      const checkSource = (rule, value, cb) => {
        const reg = /^[0-9a-fA-F]+$/;
        if (reg.test(value)) {
          return cb()
        }
        cb(new Error('请输入16进制的字符串'))
      };
      return {
        sm4Form: {
          seleValue: '',
          pubenc: '',
          iv: '',
          type: 1,
          sourceEn: '',
          sourceDe: '',
          resultEn: '',
          resultDe: ''
        },
        isShow: 1,
        isIv: false,
        sm4FormRules: {//添加表单的验证规则对象
          seleValue: [{required: true, message: '请选择', trigger: 'change'}],
          pubenc: [{required: true, message: '请输入32位16进制的密钥', validator: checkPubenc, trigger: 'blur'}],
          iv: [{required: true, message: '请输入32位16进制的向量', validator: checkIv, trigger: 'blur'}],
          sourceEn: [{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
          sourceDe: [{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
        },
      }
    },
    methods: {
      submitSm4() {
        this.$refs.sm4FormRef.validate(async valid => {
          if (!valid) return;
          let source;
          if (this.sm4Form.type === 1) {
            source = this.sm4Form.sourceEn;
          } else if (this.sm4Form.type === 2) {
            source = this.sm4Form.sourceDe;
          }
          const {data: res} = await this.$http.post(`arithmetic/sm4?type=${this.sm4Form.type}`,
            {
              'key': this.sm4Form.pubenc,
              'source': source,
              'algo': 'SM4',
              'mode': this.sm4Form.seleValue,
              'iv': this.sm4Form.iv
            }
          );
          if (res.code === 0) {
//            this.$message.success('请求成功！');
            if (this.sm4Form.type === 1) {
              this.sm4Form.resultEn = res.data;
            } else if (this.sm4Form.type === 2) {
              this.sm4Form.resultDe = res.data;
            }
          } else {
            if (this.sm4Form.type === 1) {
              this.sm4Form.resultEn = '加密失败！';
            } else if (this.sm4Form.type === 2) {
              this.sm4Form.resultDe = '解密失败！';
            }
          }
        })
      },
      seleChange: function (value) {
        this.$refs.sm4FormRef.clearValidate();
        if (value === 'SM4/CBC/NOPADDING' || value === 'SM4/CBC/PKCS5PADDING' || value === 'SM4/CBC/PKCS7PADDING') {
          this.isIv = true
        } else {
          this.isIv = false;
          this.sm4Form.iv = '';
        }
      },
      radioChange(value) {
        this.isShow = value;
        this.$refs.sm4FormRef.clearValidate();
        this.sm4Form.result = '';
//        this.$refs.sm4FormRef.resetFields();
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>
<style lang="less" scoped>
  .el-select {
    width: 100%;
  }
</style>
