<template>
  <div style="width: 600px">
    <el-form label-width="110px">
      <el-form-item label="类型" prop="type">
        <el-radio-group v-model="sm2Form.type" @change="radioChange">
          <el-radio :label="1">加密</el-radio>
          <el-radio :label="2">解密</el-radio>
          <el-radio :label="3">签名</el-radio>
          <el-radio :label="4">验签</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <el-form :model="sm2Form" ref="sm2FormRef" :rules="sm2FormRules" label-width="110px" autocomplete="off">
      <!--加密-->
      <div v-if="isShow === 1">
        <el-form-item label="公钥" prop="pubenc">
          <el-input type="textarea" :rows="2" v-model.trim="sm2Form.pubenc"></el-input>
        </el-form-item>
        <el-form-item label="加密内容" prop="sourceEn">
          <el-input type="textarea" :rows="8" v-model.trim="sm2Form.sourceEn"></el-input>
        </el-form-item>
        <el-form-item label="结果" prop="resultEn">
          <el-input type="textarea" :readonly="true" class="areaStyle" :rows="8" v-model.trim="sm2Form.resultEn"></el-input>
        </el-form-item>
      </div>
      <!--解密-->
      <div v-else-if="isShow === 2">
        <el-form-item label="私钥" prop="encPrikey">
          <el-input type="textarea" :rows="2" v-model.trim="sm2Form.encPrikey"></el-input>
        </el-form-item>
        <el-form-item label="解密内容" prop="sourceDe">
          <el-input type="textarea" :rows="8" v-model.trim="sm2Form.sourceDe"></el-input>
        </el-form-item>
        <el-form-item label="结果" prop="resultDe">
          <el-input type="textarea" :readonly="true" class="areaStyle" :rows="8" v-model.trim="sm2Form.resultDe"></el-input>
        </el-form-item>
      </div>
      <!--签名-->
      <div v-else-if="isShow === 3">
        <el-form-item label="签名私钥" prop="signPrikey">
          <el-input type="textarea" :rows="2" v-model.trim="sm2Form.signPrikey"></el-input>
        </el-form-item>
        <el-form-item label="签名公钥" prop="pubsign">
          <el-input type="textarea" :rows="2" v-model.trim="sm2Form.pubsign"></el-input>
        </el-form-item>
        <el-form-item label="签名ID" prop="signId">
          <el-input v-model.trim="sm2Form.signId"></el-input>
        </el-form-item>
        <el-form-item label="签名内容" prop="sourceSnull" v-if="sm2Form.signId !== ''">
          <el-input type="textarea" class="areaStyle"  :rows="8" v-model.trim="sm2Form.sourceSnull"></el-input>
        </el-form-item>
        <el-form-item label="签名内容" prop="sourceS" v-else>
          <el-input type="textarea" class="areaStyle" maxlength="64" show-word-limit :rows="8" v-model.trim="sm2Form.sourceS"></el-input>
        </el-form-item>
        <el-form-item label="结果" prop="resultS">
          <el-input type="textarea" :readonly="true" class="areaStyle" :rows="8" v-model.trim="sm2Form.resultS"></el-input>
        </el-form-item>
      </div>
      <!--验签-->
      <div v-else-if="isShow === 4">
        <el-form-item label="签名公钥" prop="pubsign">
          <el-input v-model.trim="sm2Form.pubsign"></el-input>
        </el-form-item>
        <el-form-item label="签名数据" prop="signData">
          <el-input v-model.trim="sm2Form.signData"></el-input>
        </el-form-item>
        <el-form-item label="签名ID" prop="signId">
          <el-input v-model.trim="sm2Form.signId"></el-input>
        </el-form-item>
        <el-form-item label="验签内容" prop="sourceY">
          <el-input type="textarea" class="areaStyle" :rows="8" v-model.trim="sm2Form.sourceY"></el-input>
        </el-form-item>
        <el-form-item label="结果" prop="resultY">
          <el-input type="textarea" :readonly="true" class="areaStyle" :rows="8" v-model.trim="sm2Form.resultY"></el-input>
        </el-form-item>
      </div>
      <el-form-item>
        <el-button type="primary" @click="submitSm2">提交</el-button>
        <el-button @click="resetForm('sm2FormRef')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      const checkSource = (rule, value, cb) => {
        const reg = /^[0-9a-fA-F]+$/;
        if (reg.test(value)) {
          return cb()
        }
        cb(new Error('请输入16进制的字符串'))
      };
      const checkSignId=(rule, value, cb) =>{
        const reg = /^[0-9a-fA-F]+$/;
        if(value !== ''){
          if (reg.test(value)) {
            return cb()
          }
          cb(new Error('请输入16进制的字符串'))
        }else{
          cb()
        }
      };
      const check64 = (rule, value, cb) => {
        const reg = /^([0-9a-fA-F]){64}$/;
        if (reg.test(value)) {
          return cb()
        }
        cb(new Error('请输入64位16进制的字符串'))
      };
      return {
        sm2Form: {
          pubenc: '',
          encPrikey: '',
          type: 1,
          signId:'',
          signPrikey: '',
          pubsign: '',
          signData: '',
          sourceEn:'',
          sourceDe:'',
          sourceSnull:'',
          sourceS:'',
          sourceY:'',
          resultEn:'',
          resultDe:'',
          resultS:'',
          resultY:'',
          result: '',
        },
        isShow: 1,
        sm2FormRules: {
          pubenc: [{required: true, message: '请输入16进制的字符串', validator: checkSource,trigger: 'blur'}],
          encPrikey: [{required: true, message: '请输入16进制的字符串', validator: checkSource,trigger: 'blur'}],
          sourceEn: [{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
          sourceDe: [{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
          sourceSnull:[{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
          sourceS: [{required: true, message: '请输入64位16进制的字符串', validator: check64, trigger: 'blur'}],
          sourceY: [{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
          signPrikey: [{required: true,message: '请输入16进制的字符串', validator: checkSource,trigger: 'blur'}],
          pubsign: [{required: true, message: '请输入16进制的字符串', validator: checkSource,trigger: 'blur'}],
          signData: [{required: true, message: '请输入16进制的字符串', validator: checkSource, trigger: 'blur'}],
          signId:[{validator: checkSignId, trigger: 'blur'}]
        }
      }
    },
    methods: {
      submitSm2() {
        this.$refs.sm2FormRef.validate(async valid => {
          if (!valid) return;
          let data;
          if (this.sm2Form.type === 3) {//签名
            if(this.sm2Form.signId !== ''){
              data = {
                'signId':this.sm2Form.signId,
                'priKey': this.sm2Form.signPrikey,
                'pubKey': this.sm2Form.pubsign,
                'source': this.sm2Form.sourceSnull,
              };
            }else{
              data = {
                'signId':this.sm2Form.signId,
                'priKey': this.sm2Form.signPrikey,
                'pubKey': this.sm2Form.pubsign,
                'source': this.sm2Form.sourceS,
              };
            }
          } else if (this.sm2Form.type === 4) {//验签
            data = {
              'signId':this.sm2Form.signId,
              'pubKey': this.sm2Form.pubsign,
              'signData': this.sm2Form.signData,
              'source': this.sm2Form.sourceY,
            };
          } else if (this.sm2Form.type === 1) {//加密
            data = {
              'pubKey': this.sm2Form.pubenc,
              'source': this.sm2Form.sourceEn
            }
          } else if (this.sm2Form.type === 2) {//解密
            data = {
              'priKey': this.sm2Form.encPrikey,
              'source': this.sm2Form.sourceDe
            }
          }
          const {data: res} = await this.$http.post(`arithmetic/sm2?type=${this.sm2Form.type}`, data);
          if (res.code === 0) {
//            this.$message.success('请求成功！');
            const result = res.data;
            if (this.sm2Form.type === 1) {
              return this.sm2Form.resultEn = ''+result;
            }else if(this.sm2Form.type === 2){
              return this.sm2Form.resultDe = ''+result;
            }else if(this.sm2Form.type === 3){
              return this.sm2Form.resultS = "签名数据:" + result;
            }else if(this.sm2Form.type === 4){
              return this.sm2Form.resultY = ''+result;
            }
          }else {
//            return this.$message.error('请求失败!')
            if (this.sm2Form.type === 1) {
              return this.sm2Form.resultEn = '解密失败！';
            }else if(this.sm2Form.type === 2){
              return this.sm2Form.resultDe = '解密失败！';
            }else if(this.sm2Form.type === 3){
              return this.sm2Form.resultS = "签名失败！";
            }else if(this.sm2Form.type === 4){
              return this.sm2Form.resultY ='验签失败！';
            }
          }
        })
      },
      radioChange(value) {
        this.isShow = value;
        this.$refs.sm2FormRef.clearValidate();
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.sm2Form.signId='';
      }
    }
  }
</script>

